.tripcard{
    margin: auto;
    color: black;

}

.trip h1{
    font-size:2rem;
}
.tripcard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}


.t-card h4{
    text-align: center;
    margin-bottom: 1rem;
}
.t-image{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;

}
.t-image img{
    width:100%;
    height:100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.t-image:hover img{
    transform:scale(1.3);
}
.t-card h4{
    font-size: 1.3rem;
    padding: .9rem 0 .2rem 0;
}
@media screen and  (max-width:784px){

    .trip{
        margin: 4rem 2rem;
        flex-direction: row;

    }
    .tripcard{
        flex-direction: column;
    }
    .t-card{
        width: 100%;
        margin-bottom:3rem;
    }
    .t-card h4{
        margin-bottom: 1rem;
    }
    
.t-image{
    height:200px;
    overflow: hidden;
    border-radius: 7px;

}
.t-image img{
    width:100%;
    height:100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.t-image:hover img{
    transform:scale(1.3);
}

    
}
    
