*{
  padding:0;
  margin:0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
*{
  margin:1px;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
 
  
}
.app{
  text-align: center;
}
.NavbarItems{
    background-color:#fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0, 0,0, 25);
    width: 95%;
    height: 90px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    z-index:9999;
}
.navbar-logo{
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}
.nav-menu{
    display: grid;
    grid-template-columns:repeat(5,auto);
    grid-gap: 20px;
    list-style: none;
    align-items: center;

}
.menu-icons{
    display:none;
}
.nav-links{
    
    text-decoration: none;
    color: #222;
    font-size: 0.9rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}
.nav-links:hover{
    background-color: #01959a;
    color: #fff;
    border-radius:4px;
    transition:all 0.2s ease-in-out;

}
.nav-links-mobile{
    display: block;
    
}

button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius:1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
}
button:hover{
    background-color: #151516;
    color: #fff;
}

@media screen and (max-width:850px){
    .NavbarItems{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background-color: #fff;
        width:100%;
        height: auto;
        position: absolute;
        top:0;
        left:-100%;
        opacity: 0;
        align-items: stretch;
        padding:80px 0 30px 0;
        margin: 0;
        text-align:center;
      
    }

    .nav-menu.active{
left: 0;
opacity: 1;
z-index: -1;
transition: 0.3s ease-in-out;
border-radius: 13px;
    }
    .nav-links{
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
    }
    .nav-links:hover{
        background-color: #01959a;
        transition: none;

    }
    button{
        display: none;
    }
    .nav-links-mobile{
        display: block;
        text-align:center;
        padding: 1.5rem;
        margin:auto;
        background:#0f0f0f;
        text-decoration: none;
        color: #fff;
        font-size: 1.2rem;
        font-weight:600;
        letter-spacing: 2px;
        
    }
    .menu-icons{
        display:block;
        cursor: pointer;
    }
    .menu-icons{
        font-size: 1.2rem;
        color: #222;
    }
    
}

    

.hero{
    width:100%;
    height:100%;
    position: relative;

}
.hero-mid{
    width: 100%;
    height:100%;
    position:relative;
    margin-bottom: 1rem;
    
    
   

}
.hero-mid h1{
    margin-top: 2rem;

}
img{
    margin-top: 2rem;
    width:100%;
    height: 15%;
    object-fit: cover;
    border-radius: 2px;
}
.hero-text{
    position: absolute;
    top: 55%;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
    width:100%;
}
.hero-text h1{
    font-size: 2rem;
    font-weight:800;
    background-color:#fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent;
    text-align: center;
}
.hero-text p{
    font-size: 1rem;
    font-weight:800;
    background-color:#fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent;
    text-align: center;
}

@media screen and (max-width: 555px)
{
    .hero-text h1{
        padding: 10px 20px;
    }
    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }
    img{
        width:100%;
        height: 100%;
        object-fit: cover;
    }
}
    

